import { combineReducers } from "redux";

// REDUCERS
import user from "../reducers/user";
import createProject from "../reducers/project/createProject";
import getProjects from "../reducers/project/getProjects";
import currentProject from "../reducers/project/currentProject";
import updateUserProfile from "../reducers/project/updateUserProfile";
import updateUserPassword from "../reducers/project/updateUserPassword";

import updateProjectDetails from "../reducers/project/updateProjectDetails";
import getProjectTeachers from "reducers/project/getProjectTeachers";
import addTeacher from "reducers/project/addTeacher";

export default () =>
  combineReducers({
    user,
    project: combineReducers({
      getProjects,
      createProject,
      currentProject,
      teachers: combineReducers({
        list: getProjectTeachers,
        add: addTeacher,
      }),
      settings: combineReducers({
        updateProjectDetails,
      }),
    }),
    profile: combineReducers({
      updateUserProfile,
      updateUserPassword,
    }),
  });
