import {
  UPDATE_PROJECT_DETAILS,
  RESET_PROJECT_UPDATION_DETAILS,
} from "actions/project/types";

const initialState = {
  data: null,
  isLoading: false,
  error: null,
};

export default function (state = initialState, { type, payload }) {
  switch (type) {
    case UPDATE_PROJECT_DETAILS.TRIGGER:
      return { ...initialState, isLoading: true };
    case UPDATE_PROJECT_DETAILS.SUCCESS:
      return { ...state, data: payload };
    case UPDATE_PROJECT_DETAILS.FAILURE:
      return { ...state, error: payload };
    case UPDATE_PROJECT_DETAILS.FULFIL:
      return { ...state, isLoading: false };
    case RESET_PROJECT_UPDATION_DETAILS:
      return initialState;

    default:
      return state;
  }
}
