import { GET_PROJECT_TEACHERS } from "actions/project/types";

const initialState = {
  data: null,
  isLoading: false,
  error: null,
};

export default function (state = initialState, { type, payload }) {
  switch (type) {
    case GET_PROJECT_TEACHERS.TRIGGER:
      return { ...initialState, isLoading: true };
    case GET_PROJECT_TEACHERS.SUCCESS:
      return { ...state, data: payload };
    case GET_PROJECT_TEACHERS.FAILURE:
      return { ...state, error: payload };
    case GET_PROJECT_TEACHERS.FULFIL:
      return { ...state, isLoading: false };

    default:
      return state;
  }
}
