import {
  UPDATE_USER_PROFILE,
  RESET_PROFILE_UPDATION_DETAILS,
} from "actions/project/types";

const initialState = {
  data: null,
  isLoading: false,
  error: null,
};

export default function (state = initialState, { type, payload }) {
  switch (type) {
    case UPDATE_USER_PROFILE.TRIGGER:
      return { ...initialState, isLoading: true };
    case UPDATE_USER_PROFILE.SUCCESS:
      return { ...state, data: payload };
    case UPDATE_USER_PROFILE.FAILURE:
      return { ...state, error: payload };
    case UPDATE_USER_PROFILE.FULFIL:
      return { ...state, isLoading: false };
    case RESET_PROFILE_UPDATION_DETAILS:
      return initialState;

    default:
      return state;
  }
}
