import {
  SET_CURRENT_PROJECT,
  UNSET_CURRENT_PROJECT,
} from "actions/project/types";

const initialState = {
  data: null,
  isLoading: false,
  error: null,
};

export default function (state = initialState, { type, payload }) {
  switch (type) {
    case SET_CURRENT_PROJECT.TRIGGER:
      return { ...initialState, isLoading: true };
    case SET_CURRENT_PROJECT.SUCCESS:
      return { ...state, data: payload };
    case SET_CURRENT_PROJECT.FAILURE:
      return { ...state, error: payload };
    case SET_CURRENT_PROJECT.FULFIL:
      return { ...state, isLoading: false };

    case UNSET_CURRENT_PROJECT:
      return { ...initialState };

    default:
      return state;
  }
}
