import React from "react";

const ProfileImage = ({ imageSrc, style, className }) => {
  return (
    <div
      className={`h-16 w-16 absolute b-0 inset-x-0 right-auto left-auto ${className}`}
      style={style}
    >
      <img src={imageSrc || "/assets/images/no_image.png"} className="h-full" />
    </div>
  );
};

export default ProfileImage;
