import {
  USER_LOGGED_OUT,
  LOGIN_USER,
  REGISTER_USER,
  AUTHENTICATE,
} from "actions/types";

const initialState = {
  data: null,
  isLoading: false,
  error: null,
};

export default function (state = initialState, { type, payload }) {
  switch (type) {
    case LOGIN_USER.TRIGGER:
    case REGISTER_USER.TRIGGER:
    case AUTHENTICATE.TRIGGER:
      return { ...initialState, isLoading: true };
    case LOGIN_USER.SUCCESS:
    case REGISTER_USER.SUCCESS:
    case AUTHENTICATE.SUCCESS:
      return { ...state, data: payload };
    case LOGIN_USER.FAILURE:
    case REGISTER_USER.FAILURE:
    case AUTHENTICATE.FAILURE:
      return { ...state, error: payload };
    case LOGIN_USER.FULFIL:
    case REGISTER_USER.FULFIL:
    case AUTHENTICATE.FULFIL:
      return { ...state, isLoading: false };
    case USER_LOGGED_OUT:
      return initialState;
    default:
      return state;
  }
}
