import React, { useState } from "react";
import { useLocation, useParams, Link } from "react-router-dom";
import { connect } from "react-redux";
import PersonIcon from "@material-ui/icons/Person";
import ViewListIcon from "@material-ui/icons/ViewList";
import AddCircleOutlineIcon from "@material-ui/icons/AddCircleOutline";
import SettingsIcon from "@material-ui/icons/Settings";
import AppsIcon from "@material-ui/icons/Apps";

import history from "../history";

import {
  userIsAuthenticated,
  userIsNotAuthenticated,
  getUserName,
  getUserEmail,
} from "utils/auth.util";

// Actions
import { logout } from "actions/user";
import ProfileImage from "./ProfileImage";
import OutsideAlerter from "./OutsideAlerter";

const UserName = ({ user }) => (
  <p className="text-md text-gray-600 font-bold flex items-center">
    {getUserName(user)}
  </p>
);

const UserEmail = ({ user }) => (
  <p className="text-md text-gray-600 flex items-center">
    {getUserEmail(user)}
  </p>
);

const LogoutLink = userIsAuthenticated(({ logout }) => (
  <button
    onClick={() => logout()}
    className="bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 border-b-4 border-blue-700 hover:border-blue-500 rounded mt-10"
  >
    Logout
  </button>
));

export const Profile = ({ user, logout, projectId }) => {
  const [showModal, setShowModal] = useState(false);

  const handleProfileBtnClick = () => {
    setShowModal((prevState) => !prevState);
  };

  return (
    <div className="block lg:flex lg:items-center lg:w-auto">
      <div>
        <div
          className="inline-block text-sm px-4 py-2 leading-none rounded text-white lg:mt-0 cursor-pointer"
          onClick={handleProfileBtnClick}
        >
          <PersonIcon className="text-black" />
        </div>
        {showModal && (
          <OutsideAlerter handleClickOutside={handleProfileBtnClick}>
            <div className="max-w-sm rounded overflow-hidden shadow-lg absolute right-0 mr-4 mt-4 bg-white p-3 flex flex-col items-center justify-center pt-24 z-50">
              <ProfileImage
                imageSrc={user.profileImage}
                style={{ top: "1rem" }}
              />

              <Link
                className="bg-blue-700 py-1 px-2 text-xs rounded-full text-white w-full text-center uppercase"
                to={
                  projectId
                    ? `/project/${projectId}/user-profile`
                    : `/user-profile`
                }
              >
                View Profile
              </Link>

              <UserName user={user} />
              <UserEmail user={user} />
              <LogoutLink logout={logout} />
            </div>
          </OutsideAlerter>
        )}
      </div>
    </div>
  );
};

const ProjectsListItem = ({ icon, name }) => {
  return (
    <div className="flex flex-row items-center justify-center w-full justify-between p-2 cursor-pointer hover:bg-blue-200">
      {icon}
      <span className="text-sm w-full ml-2">{name}</span>
    </div>
  );
};

export const Projects = ({ projectId }) => {
  const [showModal, setShowModal] = useState(false);

  const handleProjectBtnClick = () => {
    setShowModal((prevState) => !prevState);
  };

  return (
    <div className="block lg:flex lg:items-center lg:w-auto">
      <div>
        <div
          className="inline-block text-sm px-4 py-2 leading-none rounded text-white lg:mt-0 cursor-pointer flex flex-row items-center"
          onClick={handleProjectBtnClick}
        >
          <AppsIcon className="text-black" />
          <span className="text-sm text-black pl-1">Projects</span>
        </div>
        {showModal && (
          <OutsideAlerter handleClickOutside={handleProjectBtnClick}>
            <div className="max-w-sm rounded overflow-hidden shadow-lg absolute right-0 mr-4 mt-4 bg-white p-1 flex flex-col items-center justify-center z-50">
              <Link className="w-full" to={`/projects`}>
                <ProjectsListItem
                  icon={<ViewListIcon />}
                  name="View all Projects"
                />
              </Link>
              <Link className="w-full" to={`/create-new-project`}>
                <ProjectsListItem
                  icon={<AddCircleOutlineIcon />}
                  name="Create New Project"
                />
              </Link>

              <Link
                className="w-full"
                to={`/project/${projectId}/project-settings/general`}
              >
                <ProjectsListItem
                  icon={<SettingsIcon />}
                  name="Project Settings"
                />
              </Link>
            </div>
          </OutsideAlerter>
        )}
      </div>
    </div>
  );
};

const ProfileLink = userIsAuthenticated(
  ({ user, logout, pageName, projectId }) => (
    <nav className="flex items-center justify-between flex-wrap w-full top-0 shadow-md h-12 bg-white">
      <div className="flex items-center flex-shrink-0 ml-6">
        <span className="font-light text-xl">
          {pageName || <Link to="/projects">BotSite</Link>}
        </span>
      </div>
      <div className="flex flex-row">
        {projectId && <Projects projectId={projectId} />}

        <Profile user={user} logout={logout} projectId={projectId} />
      </div>
    </nav>
  )
);

const Navigation = ({ logout, user, currentProject }) => {
  let location = useLocation();

  // console.log({ prams: useParams(), history });

  // const { projectId } = useParams();

  let projectId = currentProject && currentProject._id;

  // console.log("projectId = ", projectId);

  let locationPathname = location.pathname;

  if (locationPathname.includes(`/project/${projectId}`)) {
    locationPathname = locationPathname.replace(`/project/${projectId}`, "");
    location = locationPathname.split("/");
  }

  // console.log("klocation = ", locationPathname, location);
  let pageName = "";

  if (location[1] === "home") pageName = "Home";
  else if (location[1] === "users") pageName = "Users";
  else if (location[1] === "teachers") pageName = "Teachers";
  else if (location[1] === "teacher" && location[2] === "add")
    pageName = "Add Teacher";
  else if (location[1] === "project-settings" && location[2] === "general")
    pageName = "Project Settings";
  else if (location[1] === "user-profile") pageName = "User Profile";

  return (
    <ProfileLink
      user={user}
      logout={logout}
      pageName={pageName}
      projectId={projectId}
    />
  );
};

const mapStateToProps = (state) => ({
  user: state.user,
  currentProject: state.project.currentProject.data,
});

const mapDisPatchToProps = { logout };

export default connect(mapStateToProps, mapDisPatchToProps)(Navigation);
