import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import HomeIcon from "@material-ui/icons/Home";
import OndemandVideoIcon from "@material-ui/icons/OndemandVideo";
import ChatBubbleOutlineIcon from "@material-ui/icons/ChatBubbleOutline";
import AssessmentIcon from "@material-ui/icons/Assessment";
import SettingsIcon from "@material-ui/icons/Settings";
import GroupIcon from "@material-ui/icons/Group";
import PermDataSettingIcon from "@material-ui/icons/PermDataSetting";
import {
  ProSidebar,
  Menu,
  MenuItem,
  SidebarHeader,
  SidebarContent,
  SubMenu,
} from "react-pro-sidebar";

import ProfileImage from "./ProfileImage";

import history from "../history";

import { userIsAuthenticated } from "utils/auth.util";

const SideNavigation = ({ user, currentProject }) => {
  // const [navCollapsed, setNavCollapsed] = useState(true);
  // const [navToggle, setNavToggle] = useState(false);

  // const [isMobile, setIsMobile] = useState(false);

  // const [width, height] = useWindowSize();

  // useEffect(() => {
  //   if (width >= 993) setIsMobile(false);
  //   else setIsMobile(true);
  // }, [width, height]);

  let projectId = currentProject && currentProject._id;

  if (!projectId) return null;

  // console.log("history = ", history.location.pathname);

  let { pathname } = history.location;

  return (
    <>
      <ProSidebar
        collapsed={false}
        toggled={true}
        className="SidebarNavigation"
      >
        <SidebarHeader className="bg-blue-900 border-b-0 relative pb-12">
          <div className="flex flex-col items-center justify-center">
            <div className="h-12 w-3/4 mb-2 border-b-2 border-gray-700 flex items-center justify-center py-1">
              <img
                src="/assets/images/botsite_logo_white_small.png"
                className="h-full"
              />
            </div>

            <p className="text-gray-200 py-1 font-semibold">
              {user && `${user.firstname} ${user.lastname}`}
            </p>
            <p className="text-gray-200 py-1 font-light text-sm">
              {user && user.email}
            </p>

            <ProfileImage
              className="bottom-0"
              imageSrc={user && user.profileImage}
              style={{ bottom: "-2rem" }}
            />
          </div>
        </SidebarHeader>
        <SidebarContent className="pt-8">
          <Menu iconShape="circle">
            <MenuItem
              icon={<HomeIcon />}
              active={
                pathname && pathname.includes(`/project/${projectId}/home`)
              }
            >
              <Link to={`/project/${projectId}/home`}>Home</Link>
            </MenuItem>
            <MenuItem icon={<OndemandVideoIcon />}>
              <a
                href={`${
                  process.env.REACT_APP_INTERACTIVE_VIDEO_DASHBOARD_BASE_URL
                }/videoInteractive?token=${
                  user && `JWT ${user.accessToken}`
                }&publisherId=${user && user.publisherId}&projectId=${
                  currentProject && currentProject._id
                }`}
                target="_blank"
                rel="noopener noreferrer"
              >
                Create Interactive Video
              </a>
            </MenuItem>
            <MenuItem icon={<ChatBubbleOutlineIcon />}>
              <a
                href={`${process.env.REACT_APP_EDUCATION_CHAT_BASE_URL}?token=${
                  user && `JWT ${user.accessToken}`
                }`}
                target="_blank"
                rel="noopener noreferrer"
              >
                Chat
              </a>
            </MenuItem>
            <MenuItem icon={<AssessmentIcon />}>
              <a
                href={`${
                  process.env.REACT_APP_VIDEOBOT_ANALYTICS_DASHBOARD_BASE_URL
                }?token=${user && `JWT ${user.accessToken}`}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                Video Bot Analytics
              </a>
            </MenuItem>
            <SubMenu title="Settings" icon={<SettingsIcon />} className="">
              {/* <MenuItem icon={<GroupIcon />}>Users &amp; Groups</MenuItem> */}
              <MenuItem
                active={
                  pathname &&
                  pathname.includes(`/project/${projectId}/teachers`)
                }
              >
                <Link to={`/project/${projectId}/teachers`}>Teachers</Link>
              </MenuItem>
              <MenuItem
                icon={<PermDataSettingIcon />}
                active={
                  pathname &&
                  pathname.includes(
                    `/project/${projectId}/project-settings/general`
                  )
                }
              >
                <Link to={`/project/${projectId}/project-settings/general`}>
                  Project Settings
                </Link>
              </MenuItem>
            </SubMenu>
          </Menu>
        </SidebarContent>
      </ProSidebar>
    </>
  );
};

const mapStateToProps = (state) => ({
  user: state.user.data && state.user.data.data,
  currentProject: state.project.currentProject.data,
});

export default userIsAuthenticated(connect(mapStateToProps)(SideNavigation));
