import { CREATE_PROJECT, RESET_CREATE_PROJECT } from "actions/project/types";

const initialState = {
  data: null,
  isLoading: false,
  error: null,
};

export default function (state = initialState, { type, payload }) {
  switch (type) {
    case CREATE_PROJECT.TRIGGER:
      return { ...initialState, isLoading: true };
    case CREATE_PROJECT.SUCCESS:
      return { ...state, data: payload };
    case CREATE_PROJECT.FAILURE:
      return { ...state, error: payload };
    case CREATE_PROJECT.FULFIL:
      return { ...state, isLoading: false };

    case RESET_CREATE_PROJECT:
      return initialState;
    default:
      return state;
  }
}
