import {
  USER_LOGGED_OUT,
  LOGIN_USER,
  REGISTER_USER,
  AUTHENTICATE,
} from "actions/types";

// Service
import loginUserService from "services/api/auth/loginUser.service";
import registerUserService from "services/api/auth/registerUser.service";
import authenticateService from "services/api/auth/authenticate.service";

import {
  addUserToLocalStorage,
  removeUserFromLocalStorage,
} from "utils/auth.util";

export const login = (data) => async (dispatch) => {
  try {
    dispatch({ type: LOGIN_USER.TRIGGER });

    dispatch({ type: LOGIN_USER.REQUEST });

    let res = await loginUserService(data);

    // console.log("res = ", res);

    dispatch({
      type: LOGIN_USER.SUCCESS,
      payload: res,
    });

    addUserToLocalStorage(res.data.accessToken);
  } catch (err) {
    let error = {};

    if (err.response) {
      error = err.response.data;
    } else {
      error.message = err.message ? err.message : err;
    }
    dispatch({
      type: LOGIN_USER.FAILURE,
      payload: error,
    });
  } finally {
    dispatch({ type: LOGIN_USER.FULFIL });
  }
};

export const register = (data) => async (dispatch) => {
  try {
    dispatch({ type: REGISTER_USER.TRIGGER });

    dispatch({ type: REGISTER_USER.REQUEST });

    let res = await registerUserService(data);

    // console.log("res = ", res);

    dispatch({
      type: REGISTER_USER.SUCCESS,
      payload: res,
    });

    addUserToLocalStorage(res.data.accessToken);
  } catch (err) {
    let error = "Something went wrong";

    if (err.response) {
      error = err.response.data;
    } else {
      error.message = err.message ? err.message : err;
    }
    dispatch({
      type: REGISTER_USER.FAILURE,
      payload: error,
    });
  } finally {
    dispatch({ type: REGISTER_USER.FULFIL });
  }
};

export const logout = () => (dispatch) => {
  removeUserFromLocalStorage();
  dispatch({
    type: USER_LOGGED_OUT,
  });
};

export const authenticate = (token) => async (dispatch) => {
  try {
    dispatch({ type: AUTHENTICATE.REQUEST });

    let res = await authenticateService(token);

    dispatch({
      type: AUTHENTICATE.SUCCESS,
      payload: res,
    });

    addUserToLocalStorage(res.data.accessToken);
  } catch (err) {
    dispatch(logout());

    //   let error = "Something went wrong";

    //   if (err.response) {
    //     error = err.response.data;
    //   } else {
    //     error.message = err.message ? err.message : err;
    //   }
    //   dispatch({
    //     type: AUTHENTICATE.FAILURE,
    //     payload: error,
    //   });
    // } finally {
    //   dispatch({ type: AUTHENTICATE.FULFIL });
    // }
  }
};
