import React from "react";
import { Route, Switch } from "react-router-dom";
import { withRouter } from "react-router";
import loadable from "@loadable/component";

import {
  userIsNotAuthenticated,
  userIsAuthenticatedRedir,
  userIsNotAuthenticatedRedir,
} from "utils/auth.util";
// import Navigation from "elements/Navigation";

// Components
// const PrivateRoute = loadable(() => import("./PrivateRoute"));

import Navigation from "elements/Navigation";
import SideNavigation from "elements/SideNavigation";

const LoginPage = loadable(() => import("./LoginPage"));
const RegisterPage = loadable(() => import("./RegisterPage"));
const ForgotPassword = loadable(() => import("./ForgotPassword"));
const ResetPassword = loadable(() => import("./ResetPassword"));
const ProjectHome = loadable(() => import("./Dashboard/ProjectHome"));
const UserProfile = loadable(() => import("./Dashboard/UserProfile"));
const ProjectGeneralSetting = loadable(() =>
  import("./Dashboard/ProjectSettings/ProjectGeneralSetting")
);
const Teachers = loadable(() => import("./Dashboard/Teachers"));
const AddTeacher = loadable(() => import("./Dashboard/Teachers/AddTeacher"));

const ProjectsPage = loadable(() => import("./ProjectsPage"));
const CreateNewProjectPage = loadable(() => import("./CreateNewProjectPage"));
const HomePage = loadable(() => import("./HomePage"));

const Routing = () => {
  const routes = [
    {
      isPrivate: false,
      path: "/",
      exact: true,
      component: withRouter(HomePage),
    },
    {
      isPrivate: false,
      path: "/login",
      exact: true,
      component: withRouter(userIsNotAuthenticatedRedir(LoginPage)),
    },
    {
      isPrivate: false,
      path: "/register",
      exact: true,
      component: withRouter(userIsNotAuthenticatedRedir(RegisterPage)),
    },
    {
      isPrivate: false,
      path: "/forgot-password",
      exact: true,
      component: withRouter(userIsNotAuthenticatedRedir(ForgotPassword)),
    },
    ,
    {
      isPrivate: false,
      path: "/reset-password",
      exact: true,
      component: withRouter(userIsNotAuthenticatedRedir(ResetPassword)),
    },

    {
      isPrivate: true,
      path: "/projects",
      exact: true,
      component: withRouter(userIsAuthenticatedRedir(ProjectsPage)),
    },
    {
      isPrivate: true,
      path: "/create-new-project",
      exact: true,
      component: withRouter(userIsAuthenticatedRedir(CreateNewProjectPage)),
    },
    {
      isPrivate: true,
      path: "/project/:projectId/home",
      exact: true,
      component: withRouter(userIsAuthenticatedRedir(ProjectHome)),
    },
    {
      isPrivate: true,
      path: "/project/:projectId/user-profile",
      exact: true,
      component: withRouter(userIsAuthenticatedRedir(UserProfile)),
    },
    {
      isPrivate: true,
      path: "/user-profile",
      exact: true,
      component: withRouter(userIsAuthenticatedRedir(UserProfile)),
    },
    {
      isPrivate: true,
      path: "/project/:projectId/teachers",
      exact: true,
      component: withRouter(userIsAuthenticatedRedir(Teachers)),
    },
    {
      isPrivate: true,
      path: "/project/:projectId/teacher/add",
      exact: true,
      component: withRouter(userIsAuthenticatedRedir(AddTeacher)),
    },
    {
      isPrivate: true,
      path: "/project/:projectId/project-settings/general",
      exact: true,
      component: withRouter(userIsAuthenticatedRedir(ProjectGeneralSetting)),
    },
    // {
    //   isPrivate: true,
    //   path: "/logout",
    //   exact: true,
    //   component: withRouter(userIsAuthenticatedRedir(Logout)),
    // },
    {
      isPrivate: false,
      path: "*",
      component: withRouter(() => <p>401</p>),
    },
  ];

  return (
    <>
      <div className="flex flex-row w-full h-screen">
        <SideNavigation />

        <div className="flex flex-grow flex-col overflow-y-auto">
          <Navigation />
          <div className="w-full mx-auto antialiased font-body">
            <Switch>
              {routes.map((route, i) => {
                return (
                  <Route
                    key={i}
                    path={route.path}
                    exact={route.exact}
                    component={route.component}
                  />
                );
              })}
            </Switch>
          </div>
        </div>
      </div>
    </>
  );
};

export default Routing;
